@import '../../App.scss';

.container{
    width:90%;
    margin: 0 auto;

   
}

.header{
    background-color:$light-purple; 
    box-shadow: 10px 3px 5px gray; 
    font-family: $primary-font;
   
}

/* clearfix
.header ::after {
content: "";
display: none;
clear: both;
} */

nav{
 display: flex;
 flex-flow: row wrap;
 align-items: center;
}
.logo{
    height:20px;
}

.company-name{
    color: $dark-purple;
    margin: 15px;
    font-size: 25px;
    font-family: $display-font;
}
.brand{
    display: flex;
    align-items: center;
   
}
p{
    font-family: $primary-font;
}

nav ul{
 margin:0;
 padding: 0;
 list-style:none;
 display:flex;
}

nav li{
    margin-left: 35px;
   
    /* For animation */
    /* position: relative;  */
}

a{
    color: $dark-purple;
    text-decoration:none;
    text-transform: uppercase;
    cursor: pointer;
}

nav{
    display:flex;
    justify-content: space-between;
    width: 100%;
}
/* For animation */
nav li::before{
 content: '';
 display: block;
 height: 3px;
 background-color: $dark-purple;

 /* position: absolute; */
 top: 0;
 width: 0%;

}



nav li:hover::before{
    width: 100%;
}


